const ERR_CODE: any = {
  // 基础格式 7位数 xx_xx_xx_xxx:(部门_地区_业务_类型)
  200102311: {
    err_code: '200102311',
    status: 'error',
    err_msg: 'this is a net err',
  },
  3333333: {
    err_code: '333333',
    status: 'error',
    err_msg: '没有命中错误码',
  },
};
Object.freeze(ERR_CODE);

export const checkErrCode = (errCode: any) => {
  if (+errCode === 0) {
    return false;
  }
  const NO_ERR_CODE_IN_TABLE = '3333333';
  let err;
  if (errCode in ERR_CODE) {
    err = ERR_CODE[errCode];
  } else {
    err = ERR_CODE[NO_ERR_CODE_IN_TABLE];
  }
  const {
    errCodeA, status, errMmsg, callback,
  } = err;
  callback();
  return {
    err_code: errCodeA,
    status,
    err_msg: errMmsg,
  };
};
export default ERR_CODE;
