import axios from 'axios';
import { post } from './request';

// 上传资源文件
export const uploadFile = async (files: any, path = '/assets/') => {
  const formData = new FormData();
  formData.append('assetsDirPath', path);
  formData.append('file[]', files);
  try {
    const res = await post('/api/asset/upload', formData, {
      'Content-Type': 'multipart/form-data; boundary=----WebKitFormBoundarywQfr1gRNyYgeIt9h',
    });
    return res.data;
  } catch (e) {
    return e;
  }
};

// 下载文件
export const downloadFile = async (name: string) => axios.get(name, {})
  .then((res) => (res ? res.data : undefined));

// 设置块属性
export const setWidgetAttr = async (id: string, attrs: any) => {
  try {
    const res = await post('/api/attr/setBlockAttrs', {
      id: `${id}`,
      attrs,
    });
    return res.data;
  } catch (e) {
    return e;
  }
};

// custom-excalidraw: '',

// 获取块属性
export const getWidgetAttr = async (id: string) => {
  try {
    const res = await post('/api/attr/getBlockAttrs', {
      id: `${id}`,
    });
    return res.data;
  } catch (e) {
    return e;
  }
};
